import { Box, Image, Link, Text } from '@chakra-ui/react'
import React from 'react'
import { GitHubComp } from './GitHubComp'
import { LiveNetlify } from './LiveNetlify'
import styled from "styled-components"
import ThunderTech from "../Images/ThunderTech.png"
import { ProjectCardComp } from './ProjectCardComp'



export const ProjectComp = () => {
  return (
    <DIV>

      <Box p={4} display={{ md: 'flex' }}>
        <Box flexShrink={0}>
          {/* <div class="card">
            <div class="card-content"> */}
          <Image
            className='ProjectThumbnail'
            borderRadius='lg'
            width={{ md: 280, xl: 400 }}
            height={{ sm: 200,md: 190, xl: 250 }}
            src={ThunderTech}
            alt='Thunder Tech'
          />
          {/* </div>
          </div> */}

        </Box>
        <Box mt={{ base: 4, md: 0 }} ml={{ md: 6 }}>
          <Text
            fontWeight='bold'
            textTransform='uppercase'
            fontSize='sm'
            letterSpacing='wide'
            color='teal.600'
          >
            Marketing
          </Text>
          <Link
            mt={1}
            display='block'
            fontSize='lg'
            lineHeight='normal'
            fontWeight='semibold'
            href='#'
          >
            Finding customers for your new business
          </Link>
          <Text mt={2} color='gray.500'>
            Getting a new business off the ground is a lot of hard work. Here are five
            ideas you can use to find your first customers.
          </Text>
          <div className='gitHubNetlify'>
            <div>
            <GitHubComp />
            </div>
            <div>
            <LiveNetlify />
            </div>
          </div>
        </Box>
      </Box>

    </DIV>
  )
}

const DIV = styled.div`

.ProjectThumbnail:hover{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  -ms-transform: scale(1.0); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1); 
  cursor: pointer;
}


.gitHubNetlify{
  display: flex;

}

.gitHubNetlify > div{
 margin: 20px 20px 0px 0px ;

}




`